<template>
    <v-card
        :max-height="authenticated ? 400 : 300"
        class="mx-auto"
    >
        <v-card-title class="title-font infoSize">
           <span> 
               {{title}}
            </span>
           <v-spacer></v-spacer>
           <span>
                <v-icon
                    large
                    class="mx-auto"
                >
                mdi-web
                </v-icon>
           </span>
        </v-card-title>

        <v-card-subtitle class="text-font">
            {{subtitle}}
        </v-card-subtitle>

        <v-card-text class="text-font cardBody">
            {{bodyText}}
        </v-card-text>

        <v-card-actions style="display:block">
