               >
                   <v-icon>
                        mdi-delete-forever
                   </v-icon>
               </v-btn>

                 <v-btn
                    icon
               >
                   <v-icon>
                        mdi-pencil
                   </v-icon>
               </v-btn>

            </div>

        </v-card-actions>
        

            

    </v-card>
</template>

<script>

export default {
    name: 'Footer',
    props:[
        'icon',
        'title',
        'subtitle',
        'bodyText',
        'subjectItem'
    ],

    data: () => ({

    }),

    methods:{
        viewInfo(){
            console.log(this.subjectItem);
        }
    },

    computed:{
        userFirstName() {
            return this.$store.state.userFirstName;
        },
        userLastName() {
            return this.$store.state.userLastName;
        },
        userRole() {
            return this.$store.state.userRole;
        },
        authenticated(){
            let auth = this.$store.state.authenticated;
            return auth;
        }
    }
}
</script>

<style scoped>
    .cardBody{
        height:75px; 
